html, body{

  height: 100%;

}

body {

    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin: 0;
    padding: 0;
    background: linear-gradient(180deg, rgba(9, 35,175)0%, rgba(0,0,0,1) 100%);
    color: aliceblue;


}

button{

    background: blue;
    color: white;
    padding: 0  45px;
    border: 2px solid white;
    border-radius: 8px;
    height: 50px;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
    transition: 0.4s;

}

button:hover {

    background-color: blue;

}